@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@400;500;600;700;800;900&display=swap);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
  margin: 0;
  padding: 0;
  height: 100vh;
  max-width: 100vw;
  font-family: 'Orbitron', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  letter-spacing: 3px;
  font-size: 22;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.btn {
    cursor: pointer;
}

.bg-image {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    z-index: -100;
}

@media screen and (max-width: 700px) {
    body {
        padding: 0;
    }
    .container {
        margin: 0;
        padding: 0;
    }
}

.container {
    width: 100%;
    margin: 0;
    padding-bottom: 40px;
    background-repeat: no-repeat;
    background-size: cover;
}

@media screen and (max-width: 500px) {
    body {
        background-image: url(/step-sequencer/static/media/rotate-device.0eb0da31.png);
        background-size: contain;
        background-repeat: no-repeat;
        background-color: white;
    }
    .container {
        display: none;
    }
}


.player-panel {
    position: fixed;
    top: 0;
    width: 100%;
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 2px solid rgba(0, 0, 0);
    /* border-radius: 8px; */
    font-size: 18px;
    background-image: url(/step-sequencer/static/media/brushed-aluminium.312149e7.jpg);
}
.player-controls {
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}
.paused {
    background-color: rgb(129, 172, 43);
    padding-left: 5px;
}
.playing {
    background-color: yellowgreen;
}

.play-pause-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 90px;
    width: 90px;
    border-radius: 50%;
    box-shadow: 0 0 5px black;
}

img {
    height: 80px;
}


.reset-btn {
    text-transform: uppercase;
    background-color: rgba(0, 0, 0, 0.8);
    padding: 10px;
    border-radius: 10px;
    color: yellowgreen;
}

.reset-btn p {
    margin: 0;
    padding: 0;
}

.reset-btn:active {
    transform: scale(.95);
}
#kits {
    background-color: black;
    color: yellowgreen;
    font-size: inherit;
    font-family: inherit;
    letter-spacing: inherit;
    text-transform: uppercase;
    border: none;
}

.presets-menu {
    background-color: black;
    padding: 5px;
    border-radius: 5px;
}


.tempo {
    display: inline-block;
    font-size: inherit;
    background-color: black;
    color: yellowgreen;
    border-radius: 5px;
    text-decoration: none;
    padding: 4px 0;
    min-width: 30px;
    text-align: center;
}

.current-tempo {
    margin: 0;
    background-color: black;
    color: yellowgreen;
    font-size: inherit;
    min-width: 60px;
}

button {
    background: transparent;
    box-shadow: 0px 0px 0px transparent;
    border: 0px solid transparent;
    text-shadow: 0px 0px 0px transparent;
  }
  
  button:hover {
    box-shadow: 0px 0px 0px transparent;
    border: 0px solid transparent;
    text-shadow: 0px 0px 0px transparent;
  }
  
  button:active {
    outline: none;
    border: none;
  }
  
  button:focus {
    outline: 0;
  }
.indicator-panel {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
}

.indicators {
    padding: 0 5px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    margin-top: 10px;
}

.beat-indicator:nth-child(1) {
    height: 16px;
    width: 16px;
}
.beat-indicator:nth-child(4n+5) {
    height: 16px;
    width: 16px;
}

.beat-indicator {
    height: 12px;
    width: 12px;
    background-color: black;
    border-radius: 50%;
    display: inline-block;
}

.active {
    background-color: red;
}

.samples-section {
    margin-top: 180px;
    font-size: 20px;
    text-align: center;
    padding: 0 10px;
}

.sample-row-pads {
    width: 100%;
    /* background-color: black; */
    font-size: inherit;
    display: flex;
    justify-content: space-evenly;
    height: 60px;
    /* border-radius: 10px; */
    padding: 5px;
}

.quarter {
    display: flex;
    justify-content: space-between;
}

.sample-row {
    background-color: black;
    border-radius: 10px;
    margin-top: 20px;
}

.sample-row h4 {
    margin: 0;
    color: aliceblue;
    text-align: right;
}



